.newsfeed-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.feed-column {
    width: 50%;
}


.c-page-header {
    width: 50%;
    border-bottom: 0.5px solid var(--color-border-secondary);
    margin-bottom: 40px;
}


.c-card-archive {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 40px;
    grid-template-areas: "meta meta content content content content content content";
}

.c-card-archive__date {
    font-size: 16px;
    color: var(--color-text-primary);
}

.c-card-archive__date-mini {
    display: none;
    font-weight: 400;
    color: var(--color-text-primary);
}

.c-card-archive__title {
    font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    color: var(--color-text-primary);
}

@media only screen and (max-width: 1000px) {
    .newsfeed-container .feed-column {
        width: 66%;
    }

    .newsfeed-container .c-page-header {
        width: 66%;
    }

}

@media only screen and (max-width:699px) {
    .newsfeed-container .feed-column {
        width: 66%;
    }

    .newsfeed-container .c-page-header {
        width: 66%;
    }

    .feed-column .c-card-archive {
        display: block;
    }

    .c-card-archive .c-card-archive__meta {
        font-family: "Poppins";
        font-size: 15px;
        margin-bottom: 20px;
        padding-bottom: 10px;
        border-bottom: 1px solid var(--color-border-primary);
    }

}

