.c-card-author {
    padding: 32px;
    width: 100%;
    background-color: var(--color-background-secondary);
    border-radius: 4px;
}

.c-card-author__url {
    display: block;
}

.c-card-author__name {
    font-family: 'Poppins';
}

.c-card-author__bio {
    margin-top: 8px;
    color: var(--color-text-secondary);
    font-family: '';
    font-size: 16px;
    line-height: 24px;
}

.c-card-author > * + * {
    margin-top: 20px;
    padding-top: 20px;
    border-top: 0.5px solid var(--color-border-secondary)
}

.c-card-author__posts-title {
    position: relative;
    padding-left: 24px;
    font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
}

.c-card-author__image {
    border-radius: 50%;
}
